import React, {useEffect, useRef, useState} from "react";
import './index.scss'
import { Chart, LineAdvance, Axis, Legend, Tooltip } from 'bizcharts'

interface Props {
  color: string,
  data: any[],
  keys: [string, string]
  height?: number,
}

const LineChart: React.FC<Props> = ({color, data, keys, height = 220}) => {
  const oldData = useRef<string>('')
  const [animate, setAnimate] = useState(false)
  useEffect(() => {
    let str = color
    // try {
    //   str += JSON.stringify(data)
    // }catch (_){}
    if(oldData.current !== str) {
      oldData.current = str
      setAnimate(true)
    } else {
      setAnimate(false)
    }
  }, [color, data])
  return (
    <div className='c-lineChart-wrapper'>
      <Chart
        animate={false}
        autoFit
        padding={[10, 20, 50, 40]}
        height={height}
        data={data}
        scale={{
          [keys[0]]: { range: [0, 1] },
          [keys[1]]: { range: [0, 0.9], min: 0 }
        }}>
        <Tooltip
          domStyles={{
            'g2-tooltip': {
              background: 'rgba(0,0,0,0.8)',
              boxShadow: '0 0 6px #00000099',
              color: '#cccccc'
            }
          }}/>
        <Axis name={keys[0]} line={{
          style: {
            stroke: '#ffffff',
            opacity: 0.3,
            lineWidth: 1
          }
        }} grid={false} tickLine={false}/>
        <Axis name={keys[1]} line={{
          style: {
            stroke: '#ffffff',
            opacity: 0.3,
            lineWidth: 1
          }
        }} label={{
          autoRotate: true
        }} grid={false}/>
        <Legend visible={false} />
        <LineAdvance shape="smooth" area position={`${keys[0]}*${keys[1]}`} color={color} />
      </Chart>
    </div>
  )
}

export default LineChart;
