import React, { useEffect } from 'react'
import Icon from '../../../Icon'
import './index.scss'
import { useGlobal } from '../../../../state/global/hooks'


const DyorModal: React.FC<{ onClose?: Function }> = ({ onClose }) => {
    const { unitToken } = useGlobal()

    useEffect(() => {
        let clipboard: any = null
        try {
            if (window.ClipboardJS) {
                clipboard = new window.ClipboardJS('#copy_id', {
                    text: () => '0xB526f09e81DdeA826bF77a6034816839446a9B06',
                });
                clipboard && clipboard.on('success', () => alert('success'))
                clipboard && clipboard.on('error', () => alert('fail'))
            }

        } catch (error) {
            console.error('error', error)
        }
        return () => {
            // @ts-ignore
            clipboard && clipboard.destroy()
        }
    }, [])

    return (
        <div className='dyor-modal'>
            <div className='wallet'>
                <div className='unittoken'>
                    <Icon symbol='eth' size={26} />
                    <span>{unitToken.token}</span>
                </div>
                <div className='copy-info'>
                    <Icon symbol='eth' />
                    <span>0x55d...b3197955</span>
                    <button id="copy_id">
                        <Icon symbol="copy" />
                    </button>
                    <Icon symbol='eth' />
                </div>
            </div>
            <div className='panel'>
                <div className='float-price'>1.0000</div>
                <div className="sign up">
                    <Icon symbol="up" size={16} />
                    <span>0.02%</span>
                </div>
                <div className='list'>
                    <div>
                        <span className='title'>Market Cap</span>
                        <span className='sum'>$40,325,474,418</span>
                    </div>
                    <div>
                        <span className='title'>Max Supply</span>
                        <span className='sum'>$40,325,474,418</span>
                    </div>
                    <div>
                        <span className='title'>Circulating Supply</span>
                        <span className='sum'>$40,474/$40,325,474</span>
                    </div>
                    <div>
                        <span className='title'>Full diluted Valuation</span>
                        <span className='sum'>$40,325,474,418</span>
                    </div>
                </div>
            </div>
            <div className="colum">
                <span>Market Cap</span>
                <span className='rank'>Rank #3</span>
            </div>
            <div className="colum">
                <span>Website</span>
                {/* eslint-disable-next-line no-script-url */}
                <a className="io" href="/">tether.io</a>
            </div>
            <div className="colum">
                <span>Community</span>
                <Icon symbol='twitter' />
            </div>
            <div className='modal-footer'>
                <button onClick={() => { onClose && onClose() }}>Close</button>
            </div>
        </div>
    )
}

export default DyorModal
