import './index.scss';
import SwapList from '../component/SwapList'
import MaketInfo from '../component/MaketInfo'
import { useGlobal } from '../../../state/global/hooks'
import { useList, useSetLiquidity } from '../../../state/list/hooks'

const SwapTokenList: React.FC = () => {
  const { unitToken  } = useGlobal()
  const { SwapLiquidityList, CurrentSwapLiquidity } = useList()

  const setLiquidity = useSetLiquidity()

  return (
    <div className='swaptoken-list'>
      <MaketInfo />
      <div className='swaptoken-list-bottom'>
        <p className='title'>Trading Pair</p>
        <SwapList data={SwapLiquidityList} unitToken={unitToken} setLiquidity={setLiquidity} active={CurrentSwapLiquidity ? CurrentSwapLiquidity.index : 0} />
      </div>

    </div >
  )
}

export default SwapTokenList
