import React, {useCallback, useMemo, useState} from 'react'
import './index.scss';
import cn from 'classnames';
import SimpleBar from 'simplebar-react';
import Icon from '../../../../components/Icon'
import { useView } from '../../../../state/view/hooks'
import { useList } from '../../../../state/list/hooks'
import { ago, amount } from "../../../../utils";

type TokenListProps = {
    currentTab: string,
    data: any[],
}

type MSimpleTokenListProps = {
    data: any,
    animated: boolean,
    currentTab: string,
    opt: {
        fromSymbol: string,
        toSymbol: string,
        fromDecimal: number,
        toDecimal: number
    },
    href?: string
}

const MSimpleTokenList: React.FC<MSimpleTokenListProps> = ({ href, data, animated, currentTab, opt }) => {
    const { CurrentSwapLiquidity } = useList()
    const { hash, type, value1: from, value0: to, timestamp } = data
    const { fromSymbol, toSymbol, fromDecimal, toDecimal } = opt;
    // console.log('CurrentSwapLiquidity?.type----')
    // console.log(CurrentSwapLiquidity?.type)
    // console.log(type)
    const sign = currentTab === 'liquidity' ? (type === 0 ? 'add' : 'rm') : (() => {
        if (CurrentSwapLiquidity?.type === 0) {
            return type === 3 ? 'down' : 'up'
        } else {
            return type === 3 ? 'up' : 'down'
        }
        // return (type === 2 ? 'up' : 'down')
    })()
    // console.log(data)
    return (
        <li key={hash} className={cn({ [sign]: 1, 'fade-enter-done': animated })} onClick={href ? () => {
            window.open(href)
        } : undefined}>
            <div className='col-1'>
                {currentTab === 'liquidity'
                    ? <div className={cn('sign', { [sign]: 1 })} />
                    : <Icon symbol={sign} size={16} />
                }
                <div className='amount'>
                    <div className='from-pair'>
                        <span className='num'>{amount(from, fromDecimal, 4)}</span>
                        <span>{fromSymbol}</span>
                    </div>
                    <div className='to-pair'>
                        <span className='num'>{amount(to, toDecimal, 4)}</span>
                        <span>{toSymbol}</span>
                    </div>
                </div>
            </div>
            {/*<div className='col-2'>
              <div className='value'>
                  {!isETH && <i className='color3'>{symbol}</i>}
                  {eqTokenValue.amount}
                  {isETH && <span>{symbol}</span>}
              </div>
              <div className='emoji' />
          </div>*/}
            <div className='col-3'>{ago(timestamp)}</div>
        </li>
    )
}

const TokenList: React.FC<TokenListProps> = ({ currentTab, data }) => {
    const { isMobile } = useView()
    const symbol = 'USDT'
    const { CurrentSwapLiquidity } = useList()
    // console.log('CurrentSwapLiquidity---')
    // console.log(CurrentSwapLiquidity)

    const [animated, setAnimated] = useState<boolean>(false);

    const mkHref = useCallback((hash: string) => {
        let cid = CurrentSwapLiquidity?.cid
        let href = ''
        switch (cid) {
            case 1:
                href = `https://etherscan.io/tx/${hash}`;
                break;
            case 3:
                href = `https://ropsten.etherscan.io/tx/${hash}`;
                break;
        }
        return href
    }, [CurrentSwapLiquidity])

    const TokenListElement = useMemo(() => {
        let opt = { fromSymbol: '', toSymbol: '', fromDecimal: 0, toDecimal: 0 };

        if (CurrentSwapLiquidity) {
            opt = {
                fromSymbol: CurrentSwapLiquidity.token1.symbol,
                toSymbol: CurrentSwapLiquidity.token0.symbol,
                fromDecimal: CurrentSwapLiquidity.token1.decimal,
                toDecimal: CurrentSwapLiquidity.token0.decimal
            }
        }

        return data.map((item, i) => {
            let _animated = animated && i < 1
            return <MSimpleTokenList
                key={item.hash}
                data={item}
                animated={_animated}
                currentTab={currentTab}
                opt={opt}
                href={mkHref(item.hash)}
            />
        })
    }, [CurrentSwapLiquidity, data, animated, currentTab, mkHref])

    return (
        <div className='token-list'>
            <div className='token-list-header'>
                <span>Token Amount</span>
                <span>{`Token Value, ${symbol}`}</span>
                <span>Time</span>
            </div>
            {
                isMobile ? <div className='m-list__body'>
                    <ul>
                        {TokenListElement}
                    </ul>
                </div> : <SimpleBar  forceVisible="y"> {/*style={{ height: '320px' }}*/}
                    <ul>
                        {TokenListElement}
                    </ul>
                </SimpleBar>
            }
        </div>
    )
}
export default TokenList
