import { createReducer } from '@reduxjs/toolkit'
import { setSelectedToken, toggleLikedToken } from './actions'

export interface LocalState {
  selectedToken: string,
  likedTokens: string[]
}

const initialState: LocalState = {
  selectedToken: '',
  likedTokens: []
}

export default createReducer(initialState, builder =>
  builder
    .addCase(setSelectedToken, (state, action) => {
      const token = action.payload
      if (token) state.selectedToken = token
    })
    .addCase(toggleLikedToken, (state, action) => {
      const token = action.payload
      if (token) {
        let {likedTokens} = state
        let index = likedTokens.indexOf(token)
        if(index === -1) {
          likedTokens.push(token)
        } else {
          likedTokens.splice(index, 1)
        }
      }
    })
)
