import React, { useEffect, useRef } from 'react'
import { useThrottleFn } from 'ahooks'

interface Props {
  onResize: () => void;
  tagName?: string;
  throttle?: number
  [key: string]: any
}

const ResizableElement:React.FC<Props> = ({ tagName = 'div', throttle = 0, children, onResize, ...rest }) => {
  const ref = useRef<HTMLElement | null>(null)
  const run = useThrottleFn(onResize, { wait: throttle }).run

  useEffect(() => {
    const element = ref.current
    function checkResize (mutations: any[]) {
      const el = mutations[0].target
      const w = el.clientWidth
      const h = el.clientHeight

      const isChange = mutations
        .map((m) => `${m.oldValue}`)
        .some((prev) => prev.indexOf(`width: ${w}px`) === -1 || prev.indexOf(`height: ${h}px`) === -1)
      if (!isChange) { return }
      const event = new CustomEvent('resize', { detail: { width: w, height: h } })

      el.dispatchEvent(event)
    }
    let resizeObserver: ResizeObserver;
    if (element) {
      resizeObserver = new ResizeObserver(checkResize);
      resizeObserver.observe(element);
      element.addEventListener('resize', run)
      // const observer = new MutationObserver(checkResize)
      // observer.observe(element, { attributes: true, attributeOldValue: true, attributeFilter: ['style'] })
    }

    return () => {
      if (element) {
        element.removeEventListener('resize', run)
        resizeObserver.unobserve(element)
      }
    }
  }, [run])

  return React.createElement(tagName, { ref, ...rest }, children)
}

export default ResizableElement;
