import React, { useMemo, useState } from "react";
import Icon from "../../../Icon";
import Modal from "react-modal";
import SettingModal from "../SettingModal";
import DyorModal from "../DyorModal";
import AccountModal from "../AccountModal";

import ConnectWallet from "./ConnectWallet";
import "./index.scss";

Modal.setAppElement("#root");

const WalletButton: React.FC = () => {
  const [modalEle, setModlaEle] = useState<String | null>("");
  const [showAccountModal, setAccountModal] = useState(false);
  const onClose = () => {
    setModlaEle(null);
  };

  const toggleAccountModal = () => {
    setAccountModal(!showAccountModal);
  };

  const open = useMemo(() => {
    return !!modalEle;
  }, [modalEle]);

  return (
    <>
      <div className="wallet-container">
        <button
          onClick={() => {
            setModlaEle("setting");
          }}
        >
          <Icon symbol="setting" />
        </button>
        <ConnectWallet toggleAccountModal={toggleAccountModal} />
      </div>
      <AccountModal
        isOpen={showAccountModal}
        toggleAccountModal={toggleAccountModal}
      />
      <Modal
        className="modal modal-glass"
        isOpen={open}
        closeTimeoutMS={50}
        preventScroll
        shouldFocusAfterRender={false}
        onRequestClose={() => {
          setModlaEle(null);
        }}
        overlayClassName="overlayModal"
      >
        {modalEle === "setting" && <SettingModal onClose={onClose} />}
        {modalEle === "dyor" && <DyorModal onClose={onClose} />}
      </Modal>
    </>
  );
};
export default WalletButton;
