import { useEffect, useState, useCallback } from "react";
import { useRequest } from 'ahooks';
import { getTokens, getPairs } from "../../service";
import { useDispatch } from "react-redux";
import { setTokenList, setSelectedCurrency } from "./actions";
// import {setLiqiudityList} from "../list/actions";
import { useActiveWeb3React } from '../../hooks'
import useDebounce from '../../hooks/useDebounce'
import { updateBlockNumber } from './actions'
import { useLocal } from "../local/hooks";
import { useSetLiquidityList } from '../list/hooks'

export default function GlobalUpdater(): null {
  const { library, chainId } = useActiveWeb3React()
  const { data: tokensData, run: tokensRun } = useRequest(getTokens, { defaultParams: [0] })
  const { data: pairsData, run: pairsRun } = useRequest(getPairs, { manual: true })
  const { selectedToken } = useLocal()
  const setLiquidityList = useSetLiquidityList()
  // console.log('selectedToken---')
  // console.log(selectedToken)

  const [state, setState] = useState<{ chainId: number | undefined; blockNumber: number | null }>({
    chainId,
    blockNumber: null
  })

  useEffect(() => {
    if (chainId) {
      tokensRun(chainId as number)
    }
  }, [chainId, tokensRun])

  // useEffect(() => {
  // }, [selectedToken])


  const dispatch = useDispatch()
  useEffect(() => {
    // @ts-ignore
    if (tokensData?.success && tokensData.data) {
      let data = tokensData?.data as Currency[]
      dispatch(setTokenList(data))
      const selectedCurrency = data.filter(({ name }) => name === selectedToken)[0] || data[0]
      dispatch(setSelectedCurrency(selectedCurrency || null))
      // let token = data[0].tokens[0]
      let address = selectedCurrency?.tokens[0]?.address
      if (address) {
        pairsRun(address)
      } else {
        setLiquidityList([])
      }
    }
  }, [tokensData, dispatch, pairsRun, selectedToken, setLiquidityList])

  useEffect(() => {
    // @ts-ignore
    if (pairsData?.success && pairsData.data) {
      setLiquidityList(pairsData?.data as Pairs[])
    }
  }, [pairsData, dispatch, setLiquidityList])

  const blockNumberCallback = useCallback(
    (blockNumber: number) => {
      setState(state => {
        if (chainId === state.chainId) {
          if (typeof state.blockNumber !== 'number') return { chainId, blockNumber }
          return { chainId, blockNumber: Math.max(blockNumber, state.blockNumber) }
        }
        return state
      })
    },
    [chainId, setState]
  )
  useEffect(() => {
    if (!library || !chainId) return undefined

    setState({ chainId, blockNumber: null })

    library
      .getBlockNumber()
      .then(blockNumberCallback)
      .catch(error => console.error(`Failed to get block number for chainId: ${chainId}`, error))

    library.on('block', blockNumberCallback)
    return () => {
      library.removeListener('block', blockNumberCallback)
    }
  }, [dispatch, chainId, library, blockNumberCallback])

  const debouncedState = useDebounce(state, 100);

  useEffect(() => {
    if (!debouncedState.chainId || !debouncedState.blockNumber) return
    dispatch(updateBlockNumber({ chainId: debouncedState.chainId, blockNumber: debouncedState.blockNumber }))
  }, [dispatch, debouncedState.blockNumber, debouncedState.chainId])

  return null
}
