import { createAction } from '@reduxjs/toolkit'
import { CurrentPairs, PoolTradeListType } from './reducer'

export const setLiqiudityList = createAction<Array<Pairs>>('application/setLiqiudityList')
export const setLiquidity = createAction<CurrentPairs | null>('application/setLiquidity')
export const setTargetToken = createAction<[PairToken | null, PairToken | null]>('application/setTargetToken')
export const setPoolList = createAction<PoolTradeListType>('application/setPoolList')
export const setTradingList = createAction<PoolTradeListType>('application/setTradingList')
export const setSaved = createAction<number>('application/setSaved')
