import './style.scss'
import search from './images/search.png'
import { useState} from "react";
import {useGlobal} from "../../../../state/global/hooks";
import SearchCurrency from "../../../SearchCurrency";
// import {setSelectedCurrency} from "../../../../state/global/actions";
import arrow from './images/arrow_down.png'

const Search: React.FC = () => {
  const {selectedCurrency} = useGlobal()
  const [showSearchCurrency, setShowSearchCurrency] = useState(false)
  const closeModal = () => {setShowSearchCurrency(false)}
  return selectedCurrency ? (
    <>
      <div className='c-header-search' onClick={() => {setShowSearchCurrency(true)}}>
        <div className='dd'>
          <strong>{selectedCurrency.symbol}</strong>
          {/*<strong>BSC</strong>*/}
          <img src={arrow} width={10} alt=""/>
        </div>
        <input type="text" placeholder='Search Market' readOnly />
        <button><img src={search} width={26} alt="" style={{display: 'block'}}/></button>
      </div>
      {showSearchCurrency && (
        <div className='c-header-search-modal'>
          <div className='c-header-search-mask' onClick={closeModal}/>
          <div className='c-header-search-content'>
            <SearchCurrency onChange={closeModal} />
          </div>
        </div>
      )}
    </>

  ) : <div>&nbsp;</div>
}

export default Search
