import React, { useState, useMemo } from "react";
import "./index.scss";
import cn from "classnames";
import { useGlobal } from "../../../state/global/hooks";
import { KLineChart, NavTab } from "../../../components";
import SwapList from "../../Token/component/SwapList";
import { useList, useSetLiquidity } from "../../../state/list/hooks";
import SettingModal from "../../../components/Header/component/SettingModal";
import DyorModal from "../../../components/Header/component/DyorModal";
import Modal from "react-modal";
import TradeHeader from "../../Token/component/TradeHeader";
import { Icon } from "../../../components";
import ReactTooltip from "react-tooltip";
import TokenList from "../../Token/component/TokenList";
// import {useLiquidityRecords} from

import BuyOrSell from "../components/BuyOrSell";

import {
  useLiquidityRecords,
  useTradeRecords,
  conditions,
  Condition,
} from "../../Token/LiqiudityTrade";
import { Field } from "../../../state/swap/actions";

const MSwapList = () => {
  const { unitToken } = useGlobal();
  const { SwapLiquidityList } = useList();
  const setLiquidity = useSetLiquidity();

  return (
    <SwapList
      data={SwapLiquidityList}
      unitToken={unitToken}
      setLiquidity={setLiquidity}
      active={0}
    />
  );
};

type ButtonLiquidityProps = {
  setKey: Function;
  setC: Function;
  currentTab: string;
};

const ButtonLiquidity: React.FC<ButtonLiquidityProps> = ({
  setKey,
  setC,
  currentTab,
}) => {
  return (
    <button
      className={cn("tabliquidity", { active: currentTab === "liquidity" })}
      onClick={() => setKey("liquidity")}
    >
      Liquidity
      <i data-tip data-for="clickme" data-event="click" />
      <ReactTooltip
        clickable
        id="clickme"
        place="bottom"
        effect="solid"
        globalEventOff="click"
        backgroundColor="#181d23"
      >
        <div className="tips-buttons">
          {conditions.map((item) => (
            <a onClick={() => setC(item)} key={item.type}>
              {item.text}
            </a>
          ))}
        </div>
      </ReactTooltip>
    </button>
  );
};

export default function MobileToken() {
  const [key, setKey] = useState<"swap" | "liquidity" | "trade">("swap");
  const [tabAction, setTabAction] = useState<string | null>(null);
  const [condition, setC] = useState(conditions[0]);
  const { liquidityRecords } = useLiquidityRecords(condition.type);
  const { tradeRecords } = useTradeRecords(key as "liquidity" | "trade");

  const TabFilter = (c: Condition) => {
    setC(c);
  };
  const TabAction = (key: string) => {
    setTabAction(key);
  };

  const [isOpen, ModalConnet] = useMemo(() => {
    if (!tabAction) return [false, null];
    // console.log("tabAction", tabAction);
    switch (tabAction) {
      case "buy":
        return [true, <BuyOrSell field={Field.BUY} />];
      case "sell":
        return [true, <BuyOrSell field={Field.SELL} />];

      case "dyor":
        return [true, <DyorModal />];
      case "setting":
        return [true, <SettingModal />];
      default:
        return [false, null];
    }
  }, [tabAction]);

  const SimpleBarContent = useMemo(() => {
    switch (key) {
      case "swap":
        return <MSwapList />;
      case "liquidity":
        return <TokenList currentTab={key} data={liquidityRecords} />;
      case "trade":
        return <TokenList currentTab={key} data={tradeRecords} />;
      default:
        return null;
    }
  }, [key, liquidityRecords, tradeRecords]);
  // console.log('condition', condition)

  return (
    <div className="p-mobile-token">
      <div className="chart-wrapper">
        {key === "trade" || key === "liquidity" ? (
          <TradeHeader type={key} />
        ) : (
          <KLineChart />
        )}
        {/*<KLineChart />*/}
      </div>
      <div className={cn("switch", { [key]: 1 })}>
        <button
          onClick={() => setKey("swap")}
          className={cn("tabswap", { active: key === "swap" })}
        >
          Trading Pair
        </button>
        <ButtonLiquidity setC={TabFilter} setKey={setKey} currentTab={key} />
        <button
          onClick={() => setKey("trade")}
          className={cn({ active: key === "trade" })}
        >
          Trading
        </button>
      </div>
      <div className="list-wrapper">{SimpleBarContent}</div>
      <NavTab onAction={TabAction} />
      <Modal
        className="modal modal-glass"
        isOpen={isOpen}
        closeTimeoutMS={50}
        preventScroll={true}
        shouldFocusAfterRender={false}
        onRequestClose={() => {
          setTabAction(null);
        }}
        overlayClassName="overlayModal"
      >
        {ModalConnet}
        {/* <BuyOrSell field={Field.BUY} /> */}
      </Modal>
    </div>
  );
}
