import React, { ReactElement, useMemo, useState } from 'react'
import './index.scss';
import Icon from '../Icon'
import cn from 'classnames'
// import {useView} from "../../state/view/hooks";
import {Footer} from "../../components"

type TokenSkeletonProps = {
  children: Array<ReactElement | null>
}

const TokenSkeleton: React.FC<TokenSkeletonProps> = ({ children }) => {
  // const {view} = useView()
  const Elements = useMemo(() => {
    if (children) {
      const len = children.length
      if (len >= 3) {
        return children.slice(0, 3)
      }
      const _children = children.slice(0);
      for (let i = 0, l = len - 3; i < l; i++) {
        _children.push(null)
      }
      return _children
    } else {
      return [null, null, null]
    }
  }, [children])

  const [leftOn, setLefton] = useState<boolean>(true)
  const [rightOn, setRighton] = useState<boolean>(true)

  return (
    <div className='token-skeleton'>
      <div className='token-skeleton__center'>
        <div className={cn('token-skeleton__left', {
          open: leftOn,
          close: !leftOn
        })}>
          <button
            onClick={() => {
              setLefton(!leftOn)
            }}
          ><Icon symbol='arrow' size={20} /></button>
          {Elements[0]}
        </div>
        <div className='token-skeleton__main'>
          {Elements[1]}
        </div>
        <div className={cn('token-skeleton__right', {
          open: rightOn,
          close: !rightOn
        })}
        >
          {Elements[2]}
          <button
            onClick={() => {
              setRighton(!rightOn)
            }}
          ><Icon symbol='arrow' size={20} /></button>
        </div>
      </div>
      <Footer />
    </div>
  )
}
export default TokenSkeleton;
