import { createReducer } from '@reduxjs/toolkit'
import { FILE } from 'node:dns';
import { Field, typeInput, resetInuptValue } from './actions'

export interface SwapState {
    readonly [Field.BUY]: {
        readonly typedValue: string,
    }
    readonly [Field.SELL]: {
        readonly typedValue: string,

    }
    readonly [Field.INPUT]: {
        readonly currencyId: string | undefined
    }
    readonly [Field.OUTPUT]: {
        readonly currencyId: string | undefined
    }
}

const initialState: SwapState = {
    [Field.BUY]: {
        typedValue: ''
    },
    [Field.SELL]: {
        typedValue: ''
    },
    [Field.INPUT]: {
        currencyId: ''
    },
    [Field.OUTPUT]: {
        currencyId: ''
    }
}

export default createReducer<SwapState>(initialState, builder =>
    builder.addCase(typeInput, (state, { payload: { field, typedValue } }) => {
        if (field === Field.BUY) {
            return {
                ...state,
                [Field.BUY]: {
                    typedValue
                }
            }
        }
        if (field === Field.SELL) {
            return {
                ...state,
                [Field.SELL]: {
                    typedValue
                }
            }
        }
    }).addCase(resetInuptValue, (state) => {
        return {
            ...state,
            [Field.BUY]: { typedValue: "" },
            [Field.SELL]: { typedValue: "" },
        }
    })
);