import {useCallback, useEffect} from "react";
import { useThrottleFn } from 'ahooks';
import {useView} from "./hooks";
import {getViewType} from "../../utils";

export default function ViewUpdater(): null {
  const {view, setView} = useView();

  const set = useCallback((): void => {
    const _view = getViewType()
    if (view !== _view) {
      setView(_view)
      document.body.className = `view${_view}`
    }
  }, [setView, view]);

  const { run } = useThrottleFn(set, { wait: 100 });

  useEffect(() => {
    window.addEventListener('resize', run)
    return () => {
      window.removeEventListener('resize', run)
    }
  }, [run])

  return null
}
