const SimpleBarRow: React.FC<SimpleBarRowProps> = ({ data, isETH, index, setLiquidity, active }) => {
    const {selectedCurrency} = useGlobal()
    const {TargetToken} = useList()

    const handleSetLiquidity = useCallback((event) => {
        let type: 0 | 1 = 0
        try{
            if (selectedCurrency?.tokens[0].address !== data.token0.address) type = 1
        } catch (_) {}
        event.preventDefault();
        setLiquidity({...data, index, type: type})
    }, [data, index, selectedCurrency?.tokens, setLiquidity])

    const token = data[TargetToken?.address === data.token0.address ? 'token1' : 'token0']
    // const token = OtherToken

    // console.log(data);
    return TargetToken ? (
      // <div className='scroll-wrapper'>
          <div className={cn('swapbody__item', {active})} >
              <a onClick={active ? undefined : handleSetLiquidity}>
                  <div className='name'>
                      <div className='pair'>
                          <button><Icon symbol={data.type === 0 ? 'uni' : 'unKnow'} radius /></button>
                          <button><Icon iconSrc={token.logo} radius /></button>
                          <div>
                              <span>{token.symbol}</span>
                              <span className='color2'>{TargetToken?.symbol}</span>
                          </div>
                      </div>
                  </div>
                  <div className={cn('volume', { center: !isETH })}>
                    <span>
                        {/*{!isETH && <i className='color3'>$</i>}*/}
                        {amount(data.volume, TargetToken.decimal)}
                    </span>
                      {/*<span className='color3'>USDT</span>*/}
                  </div>
                  <div className={cn('liquidity', { center: !isETH })}>
                    <span>
                        {/*{!isETH && <i className='color3'>$</i>}*/}
                        {amount(data.liquidity, TargetToken.decimal)}
                    </span>
                      {/*<span className='color3'>USDT</span>*/}
                  </div>
                  {/*<div className='price'>
                    <span>
                        {!isETH && <i className='color3'>$</i>}
                        {price.value}
                    </span>
                  <span className='color3 dy'>
                        {isETH && <span className='color3'>ETH</span>}
                      <Icon symbol='up' size={14} />
                        <i className={cn({ 'up': 1 })}>21.2%</i>
                    </span>
              </div>*/}
              </a>
          </div>
      // </div>

    ) : null
}


const List: React.FC<ListProps> = ({ data, unitToken, setLiquidity, active }) => {
    const { isMobile } = useView()

    const isETH = false
    const LiquidityElements = useMemo(() => {
        return data.map((item, i) => {
            // console.log('active----')
            // console.log(active === i)
            return (
                <SimpleBarRow
                    key={i.toString()}
                    index={i}
                    isETH={isETH}
                    symbol={unitToken.symbol}
                    data={item}
                    // setSaved={setSaved}
                    active={active === i}
                    setLiquidity={setLiquidity} />
            )
        })
    }, [data, isETH, unitToken.symbol, active, setLiquidity])

    if (!data || data.length === 0) {
        return null
    }
    // console.log('data', data)
    return (
        <div className='swap-list'>
            <div className={cn('swap-header', { isMobile: isMobile })}>
                <div>Name</div>
                <div>Volume,24h</div>
                <div>Liquidity</div>
                {/*<div>Price</div>*/}
            </div>
            {
                isMobile ? <div className='m-swap-body'>
                    {LiquidityElements}
                </div> : <SimpleBar style={{ height: '400px' }} forceVisible="y">
                    {LiquidityElements}
                </SimpleBar>
            }
        </div>
    )
}

export default List
