import React from "react";
import {Periods, periods} from '../index'
import classNames from "classnames";
import FullScreen from '../assets/fullscreen.png'

type Props = {
  kLineChart: any,
  onFullScreen: () => void,
  period: Periods,
  setPeriod: (period: Periods) => void
}
const TopBar: React.FC<Props> = ({kLineChart, onFullScreen, period, setPeriod }) => {
  return (
    <div className='kline-topBar'>
      <ul>
        {periods.map((item) => {
          return <li key={item} className={classNames({active: period === item})} onClick={() => {
            if (period !== item) setPeriod(item)
          }}>{item}</li>
        })}
        <li onClick={onFullScreen}><img src={FullScreen} alt=""/></li>
      </ul>
    </div>
  )
}

export default TopBar
