import React, { useMemo, useCallback, useState } from "react";
import cn from "classnames";
import { ChainId, Trade } from "@uniswap/sdk";
import { Field } from "../../../../state/swap/actions";
import { useUserState } from "../../../../state/user/hooks";
import { useSwapCallback } from "../../../../hooks/useSwapCallback";
import { useActiveWeb3React } from "../../../../hooks";
import { useSwapActionHandlers } from "../../../../state/swap/hooks";
import Confirm from "./confirm";
import Swapping from "./swapping";
import Complete from "./complete";
import Failure from "./failure";
import "./index.scss";

export interface Base {
  pair: any;
  trade: Trade | undefined;
}

enum SwapState {
  NOT_START,
  PENDDING,
  FAIL,
  COMPLETE,
}

interface SwapModalProps extends Base {
  title: string;
  field: Field.BUY | Field.SELL;
  toggleConfirmModal: () => void;
}

const Title = ({ text }: { text: string }) => (
  <div className="title">{text}</div>
);

const ConfirmModal: React.FC<SwapModalProps> = ({
  title,
  trade,
  field,
  pair = [],
  toggleConfirmModal,
}) => {
  const [failText, setErrorTxt] = useState("");
  const [txHash, setHash] = useState("");
  const [swap, setSwap] = useState<SwapState>(SwapState.NOT_START);

  const { account, chainId } = useActiveWeb3React();
  const { allowedSlippage } = useUserState();

  const { callback: swapCallback, error: swapCallbackError } = useSwapCallback(
    trade,
    allowedSlippage,
    account ? account : null
  );

  const { onUserInput } = useSwapActionHandlers();

  const onConfirm = useCallback(async () => {
    if (swapCallbackError || !swapCallback) {
      return;
    }
    setSwap(SwapState.PENDDING);
    const hash = await swapCallback().catch((error) => {
      if (error) {
        setSwap(SwapState.FAIL);
        setErrorTxt(error.message);
      }
    });

    if (hash) {
      setHash(hash);
      setSwap(SwapState.COMPLETE);
      onUserInput(field, "");
    }
  }, [swapCallback, swapCallbackError, field]);

  const [ModalTitle, ConfirmationModalContent] = useMemo(() => {
    switch (swap) {
      case SwapState.NOT_START:
        return [
          <Title text={`Confirm ${title}`} />,
          <Confirm
            trade={trade}
            pair={pair}
            isExactBuy={field === Field.BUY}
          />,
        ];
      case SwapState.PENDDING:
        return [null, <Swapping trade={trade ?? undefined} />];
      case SwapState.FAIL:
        return [<Title text="Error" />, <Failure msg={failText} />];
      case SwapState.COMPLETE:
        return [
          null,
          <Complete hash={txHash} chainId={chainId || ChainId.MAINNET} />,
        ];
    }
  }, [swap, trade, pair, field, title, txHash, failText]);

  const ModalFooter = useMemo(() => {
    if (swap === SwapState.NOT_START) {
      return (
        <div className="confirm-btn ">
          <button
            className={cn("trade-button", {
              dsb: swapCallbackError,
            })}
            onClick={onConfirm}
          >
            Confirm
          </button>
        </div>
      );
    }
    if (swap === SwapState.FAIL || swap === SwapState.COMPLETE) {
      return (
        <div className="confirm-btn ">
          <button
            className={cn("trade-button", { dismiss: swap === SwapState.FAIL })}
            onClick={toggleConfirmModal}
          >
            {swap === SwapState.FAIL ? "DISMISS" : "COMPLETE"}
          </button>
        </div>
      );
    }
  }, [onConfirm, toggleConfirmModal, swap]);

  return (
    <div className="modal-confirm dats-mask">
      <div className="modal-confirm__cont dats-mask__cont">
        <button className="dats-mask__close" onClick={toggleConfirmModal} />
        {ModalTitle}
        {ConfirmationModalContent}
        {ModalFooter}
      </div>
    </div>
  );
};

export default ConfirmModal;
