
const ago = (t: number) => {
  // 获取时间戳
  let showTime = ''
  let oldTime = t * 1000
  let time = new Date().getTime()
  let difference = time - oldTime
  if (difference < 0) {
    showTime = "just now"
  }
  let minute = 1000 * 60
  let hour = minute * 60
  let day = hour * 24
  let week = day * 7
  let month = day * 30
  let year = day * 365
  let yearC = difference / year
  let monthC = difference / month
  let weekC = difference / week
  let dayC = difference / day
  let hourC = difference / hour
  let minC = difference / minute

  switch (true) {
    case yearC >= 1:
      showTime = parseInt(monthC + '') + ' years ago'
      break
    case monthC >= 1:
      showTime = parseInt(monthC + '') + ' month ago'
      break
    case weekC >= 1:
      showTime = '' + parseInt(weekC + '') + ' weeks ago'
      break
    case dayC >= 1:
      showTime = '' + parseInt(dayC + '') + ' days ago'
      break
    case hourC >= 1:
      showTime = '' + parseInt(hourC + '') + ' hours ago'
      break
    case minC >= 0:
      showTime = '' + parseInt(minC + '') + 'm ago'
      break
    default:
      // console.log('其他')
  }
  return showTime
}

export default ago
