import './index.scss'
import React, {useCallback, useState} from "react";
import Icon from '../Icon'
import cn from 'classnames'

import arrow from '../../assets/images/arrow_down_3.png'
import MaketInfo from "../../pages/Token/component/MaketInfo";
import {SearchCurrency} from "../index";
import {useList} from "../../state/list/hooks";
import {useGlobal} from "../../state/global/hooks";
type Props = {
  onAction?: (key: string) => void
}

type SearchPageProps = {
  onChange: () => void
}
const SearchPage: React.FC<SearchPageProps> = ({onChange}) => {

  return (
    <div className='search-page'>
      <MaketInfo />
      <div className='search-page-content'>
        <SearchCurrency onChange={onChange} />
      </div>
    </div>
  )
}

const NavTab: React.FC<Props> = ({ onAction = () => { } }) => {
  const [showSearch, setShowSearch] = useState(false)
  const {selectedCurrency} = useGlobal()
  const toggleSearch = useCallback(() => {
    setShowSearch(!showSearch)
  }, [showSearch])
  return (
    <>
      {showSearch && <SearchPage onChange={toggleSearch} />}
      <div className='c-navTab'>
        {selectedCurrency
          ? (
            <div className='left' onClick={toggleSearch}>
              <div className='currency'>
                <p>{selectedCurrency.symbol}</p>
                <p>8,403,237<Icon symbol='up' size={14} /><i>14.8%</i></p>
              </div>
              <div className={cn('arrow', {active: showSearch})}>
                <img src={arrow} alt=""/>
              </div>
            </div>
          )
          : (<div className='left' />)
        }

        <div className='right'>
          <button className='trade-button' onClick={() => { onAction('buy') }}>Buy</button>
          <button className='trade-button sell' onClick={() => { onAction('sell') }}>Sell</button>
          <button className='icon-btn' onClick={() => { onAction('setting') }}>
            <Icon symbol="setting" size={28} />
          </button>
        </div>
      </div>
    </>
  )
}

export default NavTab
