import { axios } from "../utils";

export const getTokens = (cid: CHAIN) => {
  return axios.get(`/v1/tokens/${cid}`);
};

export const getPairs = (token: string) => {
  return axios.get(`/v1/pairs/${token}`);
};

export const getKlineData = (addr: string, typ: string = '1m', token: 0 | 1 = 0, starttime?: number) => {
  return axios.get(`/v1/pair/${addr}/kline/${token}/${typ}${starttime ? `?starttime=${starttime}` : ''}`);
};

export const getSwapRecords = (addr: string) => {
  return axios.get(`/v1/pair/${addr}/trade`);
}

export const getLiquidityRecords = (addr: string, type: 0 | 1 | 2 = 0, limit: number = 20) => {
  return axios.get(`/v1/pair/${addr}/liquidty/${type}?limit=${limit}`);
}

export const getTradeRecords = (addr: string, limit: number = 20) => {
  return axios.get(`/v1/pair/${addr}/trade?limit=${limit}`);
}

export const getLiquidityChartData = (addr: string, token: 0 | 1 = 0, isAll = false) => {
  return axios.get(`/v1/pair/${addr}/line/${token}/liquidty${isAll ? `?type=all` : ''}`);
};

export const getTradeChartData = (addr: string, token: 0 | 1 = 0, isAll = false) => {
  return axios.get(`/v1/pair/${addr}/line/${token}/trade${isAll ? `?type=all` : ''}`);
};

