import React, {useEffect} from "react";
import Typed from 'typed.js';

const code = (
  [
    "<span class='row'><i class='var'>let</i> spell = <i class='var'>dsa.Spell()</i>;</span>" +
    "\n<span class='row'>&nbsp;</span>" +
    "\n<span class='row'>spells.add{</span>" +
    "\n<span class='row'>&nbsp;&nbsp;&nbsp;&nbsp;connector: <i class='string'>\"maker\"</i>,</span>" +
    "\n<span class='row'>&nbsp;&nbsp;&nbsp;&nbsp;method: <i class='string'>\"open\"</i>,</span>" +
    "\n<span class='row'>&nbsp;&nbsp;&nbsp;&nbsp;args: <i class='string'>\"[ETH-A]\"</i>,</span>" +
    "\n<span class='row'>});</span>" +
    "\n<span class='row'>&nbsp;</span>" +
    "\n<span class='row'>spells.<i class='var'>cast()</i>;</span>"
  ]
)

const CodeTyping = () => {
  useEffect(() => {
    let typed = new Typed('#code',
      {
        typeSpeed: 30,
        showCursor: false,
        contentType: 'html',
        strings: code,
        loop: true,
        backDelay: 4000
      });

    return () => {
      typed.destroy()
    }
  }, [])

  return (
    <div className='right'>
      <div className='index'>
        <span className='row'>1</span>
        <span className='row'>2</span>
        <span className='row'>3</span>
        <span className='row'>4</span>
        <span className='row'>5</span>
        <span className='row'>6</span>
        <span className='row'>7</span>
        <span className='row'>8</span>
        <span className='row'>9</span>
      </div>
      <div className='code' id='code' />
    </div>
  )
}

export default CodeTyping;
