import React, { useState, useMemo } from "react";
import "./index.scss";
import cn from "classnames";

import walletIcon from "../../assets/images/icon/wallet2.png";
import walletIcon2 from "../../assets/images/icon/wallet2_2.png";
import settingIcon from "../../assets/images/icon/setting2.png";
import searchIcon from "../../assets/images/icon/search.png";
import favoriteActive from "../../assets/images/icon/favorite_active.png";
import favorite from "../../assets/images/icon/favorite.png";
import upIcon from "../../assets/images/icon/up.png";
import downIcon from "../../assets/images/icon/down.png";
import ethIcon from "../../assets/images/icon/ETH.png";
import switchIcon from "../../assets/images/icon/switch.png";
import dyorIcon from "../../assets/images/icon/dyor.png";
import arrowIcon from "../../assets/images/icon/arrow.png";
import discordIcon from "../../assets/images/icon/discord.png";
import twitterIcon from "../../assets/images/icon/twitter.png";
import telegramIcon from "../../assets/images/icon/telegram.png";
import copyIcon from "../../assets/images/icon/copy.png";
import searchRightIcon from "../../assets/images/icon/search-right.png";
import uniIcon from "../../assets/images/icon/uni.jpg";
import unKnowIcon from "../../assets/images/icon/unKonw.png";
import arrow2Icon from "../../assets/images/icon/arrow_2.png";
import copy2Icon from "../../assets/images/icon/copy_2.png";
import successIcon from "../../assets/images/icon/success.png";
import failIcon from "../../assets/images/icon/fail.png";

const IconType: { [title: string]: string } = {
  wallet: walletIcon,
  wallet2: walletIcon2,
  setting: settingIcon,
  search: searchIcon,
  favorite: favorite,
  favoriteActive: favoriteActive,
  up: upIcon,
  down: downIcon,
  eth: ethIcon,
  switch: switchIcon,
  dyor: dyorIcon,
  arrow: arrowIcon,
  telegram: telegramIcon,
  discord: discordIcon,
  twitter: twitterIcon,
  copy: copyIcon,
  searchRight: searchRightIcon,
  uni: uniIcon,
  unKnow: unKnowIcon,
  arrow2: arrow2Icon,
  copy2: copy2Icon,
  success: successIcon,
  fail: failIcon,
};

type IconProps = {
  symbol?: string;
  size?: number;
  radius?: boolean;
  iconSrc?: string;
};

const Icon: React.FC<IconProps> = ({
  radius = false,
  size = 24,
  symbol,
  iconSrc,
}) => {
  const [status, setStatus] = useState<boolean>(false);

  const unKnow = useMemo(() => {
    if (!symbol && !IconType[symbol as string] && !iconSrc) {
      console.error(`Invalid icon symbol: ${symbol}`);
      return true;
    }
    return status;
  }, [symbol, iconSrc, status]);

  return (
    <div
      className={cn("dats-icon", { unknow: unKnow, [`icon-${symbol}`]: 1 })}
      style={{
        width: size + "px",
        height: size + "px",
      }}
    >
      {!unKnow && (
        <img
          src={iconSrc || IconType[symbol as string]}
          alt=""
          onError={() => {
            setStatus(true);
          }}
          style={{
            borderRadius: radius ? "50%" : "unset",
          }}
        />
      )}
    </div>
  );
};

export default Icon;
