import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState, } from '../index'
import { CurrentPairs, ListState, ListType, PoolTradeListType } from './reducer'
import { setLiqiudityList, setLiquidity, setPoolList, setTradingList, setSaved, setTargetToken } from './actions'
import { useGlobal } from "../global/hooks";

export function useList(): ListState {
    return useSelector((state: AppState) => state.list)
}

export function useSetLiquidity(): (data: CurrentPairs | null) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: CurrentPairs | null) => {
        dispatch(setLiquidity(data))
        let targetToken = null
        let otherToken = null
        if(data) {
            if (data.type === 0) {
                targetToken = {...data.token0}
                otherToken = {...data.token1}
            }else {
                targetToken = {...data.token1}
                otherToken = {...data.token0}
            }
        }
        dispatch(setTargetToken([targetToken, otherToken]))
    }, [dispatch])
}

export function useSetLiquidityList(): (data: ListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    const { selectedCurrency } = useGlobal()
    const setLiquidity = useSetLiquidity()
    return useCallback((data: ListType) => {
        dispatch(setLiqiudityList(data))
        let type: 0 | 1 = 0
        try {
            if (selectedCurrency?.tokens[0].address !== data[0].token0.address) type = 1
        } catch (_) { }
        setLiquidity(data[0] ? { ...data[0], index: 0, type } : null)
    }, [dispatch, selectedCurrency?.tokens, setLiquidity])
}

export function useSetPoolList(): (data: PoolTradeListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: PoolTradeListType) => {
        dispatch(setPoolList(data))
    }, [dispatch])
}

export function useSetTradeList(): (data: PoolTradeListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: PoolTradeListType) => {
        dispatch(setTradingList(data))
    }, [dispatch])
}
export function useFavtorite(): (index: number) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((index: number) => {
        dispatch(setSaved(index))
    }, [dispatch])
}
