
import { createReducer } from '@reduxjs/toolkit'
import { updateUserDeadline, addAllowSippage } from './actions'

export interface UserState {
    // deadline set by user in minutes, used in all txns
    userDeadline: number,
    allowedSlippage: number | undefined
}


export const initialState: UserState = {
    userDeadline: 20,
    allowedSlippage: 10
}

export default createReducer(initialState,
    builder => builder.
        addCase(updateUserDeadline, (state, { payload: { userDeadline } }) => {
            if (userDeadline) {
                state.userDeadline = userDeadline
            }
        })
        .addCase(addAllowSippage, (state, { payload: { slippage } }) => {
            if (slippage) {
                state.allowedSlippage = slippage
            }
        }))
