import React, { useMemo } from "react";
import cn from "classnames";
import { Percent, CurrencyAmount } from "@uniswap/sdk";
import Icon from "../../../../components/Icon";
import { ONE_BIPS } from "../../../../constants";
import { useUserState } from "../../../../state/user/hooks";
import {
  computeSlippageAdjustedAmounts,
  computeTradePriceBreakdown,
  warningSeverity,
} from "../../../../utils/prices";
import { Base } from "./index";

interface PriceImpactProps {
  priceImpact?: Percent;
  severity?: 0 | 1 | 2 | 3 | 4;
}

export const PriceImpact: React.FC<PriceImpactProps> = ({
  priceImpact,
  severity,
}) => (
  <span
    className={cn({
      red1: severity === 3 || severity === 4,
      yellow2: severity === 2,
      green1: severity === 0,
    })}
  >
    {priceImpact
      ? priceImpact.lessThan(ONE_BIPS)
        ? "<0.01%"
        : `${priceImpact.toFixed(2)}%`
      : "-"}
  </span>
);

interface ConfirmModalProps extends Base {
  isExactBuy: Boolean;
}

const Confirm: React.FC<ConfirmModalProps> = ({
  trade,
  pair = [],
  isExactBuy,
}) => {
  const { allowedSlippage } = useUserState();
  const [inputCurreny, outputCurreny] = pair;

  const price = trade?.executionPrice.invert().toSignificant(6);

  const slippageAdjustedAmounts = useMemo((): CurrencyAmount | undefined => {
    if (!trade || !allowedSlippage || !trade) {
      return undefined;
    }
    return computeSlippageAdjustedAmounts(trade, allowedSlippage, isExactBuy);
  }, [allowedSlippage, trade, isExactBuy]);

  const tokenAmount = useMemo(() => {
    return {
      A: trade?.inputAmount?.toSignificant(6),
      B: trade?.outputAmount?.toSignificant(6),
    };
  }, [trade]);

  const { priceImpactWithoutFee, realizedLPFee } = useMemo(
    () => computeTradePriceBreakdown(trade),
    [trade]
  );

  return (
    <div className="swap-confirm__cont">
      <div className="A-B">
        <div className="input">
          <span>{tokenAmount.A}</span>
          <div>
            <Icon iconSrc={inputCurreny?.logo} />
            <span>{inputCurreny?.name}</span>
          </div>
        </div>
        <Icon symbol="arrow2" />
        <div className="input">
          <span>{tokenAmount.B}</span>
          <div>
            <Icon iconSrc={outputCurreny?.logo} />
            <span>{outputCurreny?.name}</span>
          </div>
        </div>
      </div>
      <p className="estimate">
        {isExactBuy
          ? `Output is estimated. You will receive at least `
          : `Input is estimated. You will sell at most `}
        <b>
          {slippageAdjustedAmounts?.toSignificant(6)}{" "}
          {isExactBuy ? outputCurreny?.symbol : inputCurreny?.symbol}
        </b>
        {" or the transaction will revert."}
      </p>
      <div className="impact">
        <p>
          <span>Price</span>
          <span>{`${price} ${inputCurreny?.name} per ${outputCurreny?.name}`}</span>
        </p>
        <p>
          <span>{isExactBuy ? "Minimum received" : "Maximum sold"}</span>
          <span>{`${slippageAdjustedAmounts?.toSignificant(4) ?? "-"} ${
            isExactBuy ? outputCurreny?.name : inputCurreny?.name
          }`}</span>
        </p>
        <p>
          <span>Price Impact</span>
          <PriceImpact
            priceImpact={priceImpactWithoutFee}
            severity={warningSeverity(priceImpactWithoutFee)}
          />
        </p>
        <p>
          <span>Liquidity Provider Fee</span>
          <span>
            {realizedLPFee
              ? realizedLPFee?.toSignificant(6) + " " + inputCurreny.symbol
              : "-"}
          </span>
        </p>
      </div>
    </div>
  );
};

export default Confirm;
