import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import Token from './pages/Token';
import MobileToken from './pages/mobile/Token';
import Home from './pages/Home';
import { Web3ReactManager, AppHeader } from './components'
import { useView } from "./state/view/hooks";
// import { axios } from './utils'
// import Gmodal from './components/Modal'
const App: React.FC = () => {
  const { isMobile } = useView()

  return (
    <Suspense fallback={null}>
      {/* <Gmodal /> */}
      <Web3ReactManager>
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path="/swap" exact>
            <div className='page-swap'>
              <AppHeader />
              {isMobile ? <MobileToken /> : <Token />}
            </div>
          </Route>
        </Switch>
      </Web3ReactManager>
    </Suspense>
  );
};

export default App;
