import { useState, useEffect, useRef, useCallback, useMemo } from "react";
import { CurrencyAmount } from "@uniswap/sdk";
import cn from "classnames";
import { Icon } from "../../../../components";
import { useSwapActionHandlers } from "../../../../state/swap/hooks";
import { InputAndOutputTokenType } from "../index";
import { Field } from "../../../../state/swap/actions";
import Setting from "../Setting";
import { useView } from "../../../../state/view/hooks";
import down from "./down.svg";
import "./index.scss";

type Props = {
  selectToken: boolean;
  field: Field;
  value: string;
  currency: InputAndOutputTokenType | undefined;
  balanceIn: CurrencyAmount | undefined;
};

const percent = [25, 50, 75, 100];

const Input: React.FC<Props> = ({
  selectToken = false,
  field,
  value,
  currency,
  balanceIn,
}) => {
  const { isMobile } = useView();
  const [showDropDown, setShowDropDown] = useState(false);
  const selectRef = useRef<HTMLDivElement | null>(null);

  const balance = useMemo(() => {
    if (!balanceIn) return 0;
    return balanceIn.toSignificant(6);
  }, [balanceIn]);

  const { onUserInput } = useSwapActionHandlers();

  const closeSelect = useCallback(() => {
    selectToken && setShowDropDown(false);
  }, [selectToken]);
  const stop = (event: Event) => {
    event.stopPropagation();
  };

  useEffect(() => {
    const ref = selectRef.current;
    if (selectToken && ref) {
      document.addEventListener("click", closeSelect);
      ref.addEventListener("click", stop);
    }

    return () => {
      if (selectToken && ref) {
        document.removeEventListener("click", closeSelect);
        ref.removeEventListener("click", stop);
      }
    };
  }, [closeSelect, selectToken, showDropDown]);

  const [quotient, setQ] = useState<number>(0);
  const setTypedValue = useCallback(
    (Q: number) => {
      if (!balance) return;
      let valued = (Number(balance) * Q) / 100;
      onUserInput(field, valued.toFixed(4));
      setQ(Q);
    },
    [balance, onUserInput]
  );

  return (
    <div className={cn("c-input", { showDropDown: showDropDown })}>
      <div className="c-input-title">
        <div
          className={cn("balance", { sell: Field.SELL === field && !isMobile })}
        >
          <span>From</span>
          <span>{`Balance: ${balance || "-"}`}</span>
        </div>
        {!isMobile && field === Field.SELL && <Setting />}
      </div>

      <div className="c-input-box">
        <div className="c-input-wrapper">
          <div
            className={cn("token-box", { select: selectToken })}
            onClick={
              selectToken
                ? () => {
                    setShowDropDown(!showDropDown);
                  }
                : undefined
            }
          >
            <Icon iconSrc={currency?.Logo} />
            <span>{currency?.Token?.symbol}</span>
            {selectToken && <img className="arrow" src={down} alt="" />}
          </div>
          <div className="input">
            <input
              type="text"
              placeholder="0.0"
              value={value}
              onChange={(e) => {
                setQ(0);
                onUserInput(field, e.target.value);
              }}
            />
          </div>
        </div>
        {selectToken && showDropDown && (
          <div className="c-input-select" ref={selectRef}>
            <p className="title">Select a token</p>
            <ul className="tokens">
              <li>
                <Icon symbol="eth" />
                <span>ETH</span>
              </li>
              <li>
                <Icon symbol="eth" />
                <span>ETH</span>
              </li>
              <li>
                <Icon symbol="eth" />
                <span>ETH</span>
              </li>
            </ul>
            <input type="text" placeholder="Search Token Ticker" />
          </div>
        )}
      </div>

      <div className="c-input-btns">
        {percent.map((p, i) => {
          return (
            <button
              key={p}
              onClick={() => {
                setTypedValue(p);
              }}
              className={quotient === p ? "active" : ""}
            >
              {p + "%"}
            </button>
          );
        })}
      </div>
    </div>
  );
};

export const EstimateValue: React.FC<{
  // price: string;
  currency: InputAndOutputTokenType | undefined;
  value: string | undefined;
}> = ({ currency, value }) => {
  return (
    <div className={cn("c-input")}>
      <p className="c-input-title">To(estimated)</p>
      <div className="c-input-box">
        <div className="c-input-wrapper">
          <div className={cn("token-box")}>
            <Icon iconSrc={currency?.Logo} />
            <span>{currency?.Token?.symbol || "-"}</span>
          </div>
          <div className="input">
            <span className={cn("estimate-value", { Color: !!value })}>
              {value || "0.0"}
            </span>
          </div>
        </div>
      </div>
      {/* <div className="c-input-rate">
        <span>Price</span>
        <span>{price || "-"}</span>
      </div> */}
    </div>
  );
};

export default Input;
