import React from 'react'
import './index.scss';
import DatsLogo from './component/DatsLogo';
import WalletButton from './component/WalletButton';
import Search from './component/Search';
import {useView} from "../../state/view/hooks";
import Logo from '../../assets/images/logo_2.png';
import {Link} from "react-router-dom";
import ConnectWallet from './component/WalletButton/ConnectWallet'

const Header: React.FC = () => {
  const {isMobile} = useView()

  return isMobile
    ? (
      <div className='c-header-mobile'>
        <Link className='logo' to='/'>
          <img src={Logo} alt='' />
        </Link>
        <ConnectWallet />
      </div>
    )
    : (
      <div className='c-header'>
        <DatsLogo />
        <div className='c-header-right'>
          <Search />
          <WalletButton />
        </div>
      </div>
    )

}
export default Header
