import { useMemo, useEffect, useState } from "react";
import Identicon from "../../../Identicon";
import Icon from "../../../Icon";
import { shortenAddress } from "../../../../utils";
import { useWeb3React } from "@web3-react/core";
import cn from "classnames";
import Pendding from "../../../PendingLoader";

import {
  useAllTransactions,
  isTransactionRecent,
  newTransactionsFirst,
} from "../../../../state/transactions/hooks";
import ClipboardJS from "clipboard";
import "./index.scss";

import { getEtherscanLink } from "../../../../utils";
const AccountModal: React.FC<{
  isOpen: Boolean;
  toggleAccountModal: () => void;
}> = ({ isOpen, toggleAccountModal }) => {
  const { account, chainId } = useWeb3React();

  const allTransactions = useAllTransactions();

  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions);
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst);
  }, [allTransactions]);

  const [copyed, setCopyed] = useState(false);
  useEffect(() => {
    const Clip = new ClipboardJS(".copy");
    Clip.on("success", () => {
      setCopyed(true);
      let t = setTimeout(() => {
        clearTimeout(t);
        setCopyed(false);
      }, 3000);
    });
    return () => {
      Clip.destroy();
    };
  }, []);
  return (
    <div className="dats-mask" style={{ display: isOpen ? "block" : "none" }}>
      <div className="dats-mask__cont account-modal__cont">
        <button className="dats-mask__close" onClick={toggleAccountModal} />
        <div className="title">Account</div>
        <div className="user-addr">
          {account && (
            <div className="addr">
              <Identicon />
              <span>{shortenAddress(account ?? "")}</span>
              <button className="copy" data-clipboard-text={account}>
                {copyed ? "Copied" : <Icon symbol="copy2" size={18} />}
              </button>
            </div>
          )}
        </div>
        <div className="transactions">
          <div className="tx-title">Recent Transactions</div>
          <div className="tx-list">
            {sortedRecentTransactions.length > 0 ? (
              sortedRecentTransactions.map((tx) => {
                const isSuccess =
                  tx.receipt?.status === 1 ||
                  typeof tx.receipt?.status === "undefined";
                const pendding = !tx?.receipt;
                return (
                  <a
                    target="_blank"
                    className="tx"
                    key={tx?.hash}
                    href={getEtherscanLink(
                      chainId ?? 0,
                      tx?.hash,
                      "transaction"
                    )}
                  >
                    <span
                      className={cn({
                        [pendding ? "" : isSuccess ? "green1" : "red1"]: 1,
                      })}
                    >
                      {`${tx?.summary || tx?.hash} ↗`}{" "}
                    </span>
                    {pendding ? (
                      <Pendding />
                    ) : (
                      <Icon symbol={isSuccess ? "success" : "fail"} />
                    )}
                  </a>
                );
              })
            ) : (
              <div className="no-data">No transactions</div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AccountModal;
