import React from "react";
import horizontalStraightLine from '../assets/horizontalStraightLine.svg'
// import horizontalRayLine from '../assets/horizontalRayLine.svg'
// import horizontalSegment from '../assets/horizontalSegment.svg'
import verticalStraightLine from '../assets/verticalStraightLine.svg'
// import verticalRayLine from '../assets/verticalRayLine.svg'
// import verticalSegment from '../assets/verticalSegment.svg'
import straightLine from '../assets/straightLine.svg'
// import rayLine from '../assets/rayLine.svg'
// import segment from '../assets/segment.svg'
import priceLine from '../assets/priceLine.svg'
import priceChannelLine from '../assets/priceChannelLine.svg'
import parallelStraightLine from '../assets/parallelStraightLine.svg'
import fibonacciLine from '../assets/fibonacciLine.svg'
import deleteSvg from '../assets/delete.svg'

const tools = [
  ["horizontalStraightLine", horizontalStraightLine],
  // ["horizontalRayLine", horizontalRayLine],
  // ["horizontalSegment", horizontalSegment],
  ["verticalStraightLine", verticalStraightLine],
  // ["verticalRayLine", verticalRayLine],
  // ["verticalSegment", verticalSegment],
  ["straightLine", straightLine],
  // ["rayLine", rayLine],
  // ["segment", segment],
  ["priceLine", priceLine],
  ["priceChannelLine", priceChannelLine],
  ["parallelStraightLine", parallelStraightLine],
  ["fibonacciLine", fibonacciLine],
]

const SideBar: React.FC<{kLineChart: any}> = ({kLineChart}) => {
  return (
    <div className='kline-sidebar'>
      <ul>
        {tools.map(([key, svg]) => {
          return <li key={key} onClick={_ => {
            kLineChart.current.createGraphicMark(key)
          }}><img src={svg} alt={key} /></li>
        })}
      </ul>
      <div className='div' />
      <ul>
        <li onClick={_ => {
          kLineChart.current.removeGraphicMark()
        }}><img src={deleteSvg} alt={'delete'} /></li>
      </ul>
    </div>
  )
}

export default SideBar
