import './style.scss'
import * as React from "react";
import classNames from "classnames";
import {useView} from "../../../../state/view/hooks";
import {useTranslation} from "react-i18next";
import Cookies from "js-cookie";
import cn from "classnames";
import Arrow from '../../../../assets/images/arrow_down.png'
import Arrow2 from './images/arrow_down2.png'
import {useCallback, useEffect, useRef, useState} from "react";
import US from './images/us.png';
// import ZH from './images/zh.png';
// import JA from './images/ja.png';
// import KO from './images/ko.png';

interface Props {
  className?: string
}

// const Languages = ['en', 'zh', 'ja', 'ko']
// const LanguagesDesc = ['English', '中文', '日本語', '한국어']
// const Flats = [US, ZH, JA, KO]

const Languages = ['en']
const LanguagesDesc = ['English']
const Flats = [US]

export const useLanguageManage = () => {
  const { i18n } = useTranslation()

  const changeLanguage = async (key: string) => {
    await i18n.changeLanguage(key)
    Cookies.set('language', key)
  }

  const active = Languages.indexOf(i18n.language)
  return {
    languages: Languages,
    languagesDesc: LanguagesDesc,
    language: i18n.language,
    languageDesc: LanguagesDesc[active] || '',
    flag: Flats[active] || '',
    flags: Flats,
    changeLanguage
  }
}

const LanguageManage: React.FC<Props> = ({children, className}) => {
  const {isMobile} = useView()
  const [showDD, setShowDD] = useState(false)
  const {languages, languagesDesc, language, changeLanguage, flag, flags} = useLanguageManage()
  const wrapper = useRef<HTMLDivElement | null>(null)

  const stop = useCallback((event: Event) => {
    event.stopPropagation();
    const key = (event.target as HTMLElement).getAttribute('data-key') as string
    if (key !== language) {
      changeLanguage(key);
      setShowDD(false);
    }
  }, [changeLanguage, language])

  useEffect(() => {
    const hide = () => {
      setShowDD(false)
    }
    const ref = wrapper.current
    if (ref) {
      document.addEventListener("click", hide);
      ref.addEventListener("click", stop);
    }

    return () => {
      if (ref) {
        document.removeEventListener('click', hide)
        ref.removeEventListener('click', stop)
      }
    }
  }, [setShowDD, stop])

  return (
    <div className={classNames('c-languageManage', {[className as string]: !!className})}>
      <span onClick={() => {setShowDD(true)}}>
        <img src={flag} className='flag' alt=""/>
        <img src={isMobile ? Arrow2 : Arrow} className='arrow' alt="" />
      </span>
      {showDD && (
        <div className='dropdown' ref={wrapper}>
          {languages.map((key, index) =>
            <div key={key} data-key={key} className={cn('item', { active: language === key })}>
              <span data-key={key}>{languagesDesc[index]}</span>
              <img src={flags[index]} data-key={key} alt=""/>
            </div>
          )}
        </div>
      )}
    </div>
  )
}

export default LanguageManage
