
import { createReducer } from '@reduxjs/toolkit'
import { setUnitToken, setLiquidity, setTokenList, setPairs, setSelectedCurrency } from './actions'
// import {Currency} from "@uniswap/sdk";

export interface LiquidityItem {
    type: string,
    saved: boolean,
    token: {
        from: string,
        to: string
    },
    trade24h: string,
    liquidityAmount: string,
    price: {
        value: string | number,
        float: string,
        parcent: string
    }
}

export interface UnitToken {
    token: 'ETH' | "USD",
    symbol: "ETH" | "$"
}

export interface GlobalState {
    readonly unitToken: UnitToken
    readonly liquidity: LiquidityItem,
    tokenList: Currency[],
    pairs: Pairs[],
    selectedCurrency: Currency | null
}

const initialState: GlobalState = {
    unitToken: { token: "ETH", symbol: "ETH" },
    liquidity: {
        type: "wallet",
        saved: false,
        token: {
            from: "OKEX",
            to: "BSC",
        },
        trade24h: "2232.23",
        liquidityAmount: "123232.12",
        price: {
            value: "12.1",
            float: "up",
            parcent: "12%",
        },
    },
    tokenList: [],
    pairs: [],
    selectedCurrency: null
}

export default createReducer(initialState, builder =>
    builder
        .addCase(setUnitToken, (state) => {
            const { unitToken } = state
            if (unitToken.symbol === "ETH") {
                state.unitToken = { token: "USD", symbol: "$" }
            } else {
                state.unitToken = { token: "ETH", symbol: "ETH" }
            }
        })
        .addCase(setLiquidity, (state, { payload }) => {
            state.liquidity = payload
        })
        .addCase(setTokenList, (state, { payload }) => {
            state.tokenList = payload
        })
        .addCase(setPairs, (state, { payload }) => {
            state.pairs = payload
        })
      .addCase(setSelectedCurrency, (state, { payload }) => {
          state.selectedCurrency = payload
      })
)
