import React, { useState, useMemo, useCallback } from "react";
import "./BuyOrSell.scss";
import { Field } from "../../../state/swap/actions";
import Input, { EstimateValue } from "../../Token/Trade/Input";

import cn from "classnames";
import _ from "lodash";

import { Token, ETHER, CurrencyAmount } from "@uniswap/sdk";
import { tryParseAmount } from "../../../state/swap/hooks";
import { useActiveWeb3React } from "../../../hooks";
import { useTradeExactIn } from "../../../hooks/Trades";

import { useSwapState } from "../../../state/swap/hooks";
import { useCurrencyBalance } from "../../../state/wallet/hooks";
import { useUserState } from "../../../state/user/hooks";
import { useList } from "../../../state/list/hooks";
import { useSwapActionHandlers } from "../../../state/swap/hooks";
import {
  ApprovalState,
  useApproveCallbackFromTrade,
} from "../../../hooks/useApproveCallback";
import { useSwapCallback } from "../../../hooks/useSwapCallback";
import {
  computeSlippageAdjustedAmounts,
  computeTradePriceBreakdown,
  warningSeverity,
} from "../../../utils/prices";

import { InputAndOutputTokenType } from "../../Token/Trade";
import { PriceImpact } from "../../Token/Trade/SwapModal/confirm";
import { Button as SwapButton } from "../../Token/Trade";
import SlippageAndDeadLineModal from "../../Token/Trade/Setting";

const BuyOrSell: React.FC<{ field: Field.BUY | Field.SELL }> = ({ field }) => {
  const [showConfirmModal, setConfirm] = useState<boolean>(false);
  const { chainId, account } = useActiveWeb3React();
  const { CurrentSwapLiquidity, TargetToken } = useList();

  //   const toggleConfirmModal = () => {
  //     setConfirm(!showConfirmModal);
  //   };

  const isExactBuy: boolean = field === Field.BUY;
  const {
    [isExactBuy ? Field.BUY : Field.SELL]: { typedValue },
  } = useSwapState();

  const [_className, btnText] = useMemo(() => {
    return isExactBuy ? ["buy", "Buy"] : ["sell", "Sell"];
  }, [isExactBuy]);

  let pair = useMemo(() => {
    if (!CurrentSwapLiquidity || !TargetToken) return null;
    const { token0, token1 } = CurrentSwapLiquidity;

    if (token0.name === TargetToken.name) {
      return [token0, token1];
    }
    return [token1, token0];
  }, [CurrentSwapLiquidity, TargetToken]);

  pair = pair ? (isExactBuy ? pair : pair?.reverse()) : [];

  const [inputToken, outputToken] = useMemo(() => {
    if (!pair || !chainId) {
      return [undefined, undefined];
    }
    return pair.map(
      (t): InputAndOutputTokenType => {
        return {
          Logo: t.logo,
          Token:
            t.name.toLocaleUpperCase() === "ETH" ||
            t.name.toLocaleUpperCase() === "WETH"
              ? ETHER
              : new Token(chainId, t.address, t.decimal, t.symbol),
        };
      }
    );
  }, [chainId, pair, isExactBuy]);

  const parsedAmount = tryParseAmount(typedValue, inputToken?.Token);
  const trade = useTradeExactIn(
    parsedAmount ?? undefined,
    outputToken?.Token ?? undefined
  );

  const { allowedSlippage } = useUserState();
  const [approval, approveCallback] = useApproveCallbackFromTrade(
    inputToken?.Token as Token,
    trade ?? undefined,
    allowedSlippage
  );

  const { callback: swapCallback, error: swapCallbackError } = useSwapCallback(
    trade,
    allowedSlippage,
    account ? account : null
  );

  const { onUserInput } = useSwapActionHandlers();

  const onSwap = useCallback(async () => {
    if (swapCallbackError || !swapCallback) {
      return;
    }
    // setSwap(SwapState.PENDDING);
    const hash = await swapCallback().catch((error) => {
      //   if (error) {
      //     setSwap(SwapState.FAIL);
      //     setErrorTxt(error.message);
      //   }
    });

    if (hash) {
      //   setHash(hash);
      //   setSwap(SwapState.COMPLETE);
      onUserInput(field, "");
    }
  }, [swapCallback, swapCallbackError, field]);

  const balanceIn = useCurrencyBalance(account ?? undefined, inputToken?.Token);

  const price = trade?.executionPrice.invert().toSignificant(6);

  const slippageAdjustedAmounts = useMemo((): CurrencyAmount | undefined => {
    if (!trade || !allowedSlippage || !trade) {
      return undefined;
    }
    return computeSlippageAdjustedAmounts(trade, allowedSlippage, isExactBuy);
  }, [allowedSlippage, trade, isExactBuy]);

  const { priceImpactWithoutFee, realizedLPFee } = useMemo(
    () => computeTradePriceBreakdown(trade),
    [trade]
  );
  const [inputCurreny, outputCurreny] = pair;

  const ButtonError = useMemo(() => {
    if (balanceIn && parsedAmount && balanceIn.lessThan(parsedAmount)) {
      return "Insufficient " + inputToken?.Token?.symbol + " balance";
    }

    if (!parsedAmount) {
      return `Enter an Amount`;
    }
  }, [balanceIn, parsedAmount, inputToken]);

  return (
    <div className={cn("buy-sell__cont", { [_className]: 1 })}>
      <div className="buy-sell__cont-header">
        {btnText}
        <SlippageAndDeadLineModal />
      </div>
      <div className="buy-sell__cont-form">
        <Input
          selectToken={false}
          field={field}
          balanceIn={balanceIn}
          currency={inputToken}
          value={typedValue}
        />
        <EstimateValue
          currency={outputToken}
          value={trade?.outputAmount?.toSignificant(6)}
        />
      </div>
      <div
        className={cn("buy-sell__cont-summary", {
          [!!typedValue ? "show" : "hidden"]: 1,
        })}
      >
        <ul>
          <li>
            <span>slippage</span>
            <span>{`${allowedSlippage ? allowedSlippage / 100 : 0.01}%`}</span>
          </li>
          <li>
            <span>Price</span>
            <span>{`${price} ${inputCurreny?.name} per ${outputCurreny?.name}`}</span>
          </li>
          <li>
            <span>{isExactBuy ? "Minimum received" : "Maximum sold"}</span>
            <span>{`${slippageAdjustedAmounts?.toSignificant(4) ?? "-"} ${
              isExactBuy ? outputCurreny?.name : inputCurreny?.name
            }`}</span>
          </li>
          <li>
            <span>Price Impact</span>
            <PriceImpact
              priceImpact={priceImpactWithoutFee}
              severity={warningSeverity(priceImpactWithoutFee)}
            />
          </li>
          <li>
            <span>Liquidity Provider Fee</span>
            <span>
              {realizedLPFee
                ? realizedLPFee?.toSignificant(6) + " " + inputCurreny.symbol
                : "-"}
            </span>
          </li>
        </ul>
      </div>
      <div className="buy-sell__cont-footer">
        <SwapButton
          btnText={btnText}
          ButtonError={ButtonError}
          _className={_className}
          approvedState={approval}
          currency={outputToken}
          onSwap={onSwap}
          onApproved={approveCallback}
        />
      </div>
    </div>
  );
};

export default BuyOrSell;
