import { useEffect, } from 'react'
import { useSetPoolList, useSetTradeList } from './hooks'
// import { ListType } from './reducer'
import { swapData, poolData, tradingData } from '../../pages/Token/data'


export default function Updater(): null {
    // const [swapList] = useState<ListType>(swapData)
    // const getSwapData = useFetchLiquidityList()
    const getPoolData = useSetPoolList()
    const getTradeData = useSetTradeList()

    useEffect(() => {
        // getSwapData(swapData)
        getPoolData(poolData)
        getTradeData(tradingData)
    }, [getPoolData, getTradeData])

    return null
}
