export function useUserState(): UserState {
    const user = useSelector<AppState>(state => state.user)
    return user as UserState
}

export function useUserActionHandlers(): {
    setDeadline: (deadLine: number) => void,
    setSlippage: (slippage: number | undefined) => void
} {
    const dispatch = useDispatch()
    const setDeadline = useCallback((deadLine: number) => {
        dispatch(updateUserDeadline({ userDeadline: deadLine }))
    }, [dispatch])

    const setSlippage = useCallback((slippage: number | undefined) => {
        dispatch(addAllowSippage({ slippage }))
    }, [dispatch])

    return {
        setDeadline,
        setSlippage
    }
}