import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState, } from '../index'
import { UnitToken, GlobalState } from './reducer'
import { setUnitToken, } from './actions'
import { useActiveWeb3React } from '../../hooks'


export function useGlobal(): GlobalState {
    return useSelector((state: AppState) => state.global)
}

export function useSwitchToken(): () => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback(() => dispatch(setUnitToken()), [dispatch])
}