import * as React from "react";
import './style.scss'
import CenterWrapper from "../CenterWrapper";
import Logo from '../../../../assets/images/logo_2.png'
import Language from '../../images/icon_language.png'
import Arrow from '../../../../assets/images/arrow_down_2.png'
import Twitter from '../../images/icon_twitter.png'
import Reddit from '../../images/icon_reddit.png'
import LinkedIn from '../../images/icon_linkedin.png'
import Telegram from '../../images/icon_telegram.png'
import Wechat from '../../images/icon_wechat.png'
import {useView} from "../../../../state/view/hooks";
import {useCallback, useEffect, useRef, useState} from "react";
import {useLanguageManage} from "../LanguageManage";
import cn from "classnames";

import DocPrivacy from '../../../../assets/docs/Privacy_Policy.pdf'
import DocCookie from '../../../../assets/docs/Cookie_Notice.pdf'
import DocTerms from '../../../../assets/docs/Terms_of_Use_Agreement.pdf'

interface Props {}
const Footer: React.FC<Props> = () => {
  const {isMobile} = useView()
  const dls = (
    <>
      <dl>
        <dt>Dprotocols</dt>
        <dd><a href="/">Docs</a></dd>
        <dd><a href="/">API</a></dd>
        <dd><a href={DocPrivacy}>Privacy policy</a></dd>
        <dd><a href={DocCookie}>Cookies</a></dd>
        <dd><a href={DocTerms}>Terms and Conditions</a></dd>
      </dl>
      <dl>
        <dt>Support</dt>
        <dd><a href="/">FAQ</a></dd>
        <dd><a href="/">Customer Support</a></dd>
        <dd><a href="/">Agreement</a></dd>
      </dl>
      <dl>
        <dt>Community</dt>
        <dd><a href="/"><img src={Twitter} width={26} alt=""/>Twitter</a></dd>
        <dd><a href="/"><img src={Reddit} width={26} alt=""/>Reddit</a></dd>
        <dd><a href="/"><img src={LinkedIn} width={26} alt=""/>Linkedin</a></dd>
        <dd><a href="/"><img src={Telegram} width={26} alt=""/>Telegram</a></dd>
        <dd><a href="/"><img src={Wechat} width={26} alt=""/>Wechat</a></dd>
      </dl>
    </>
  )

  const wrapper = useRef<HTMLDivElement | null>(null)
  const [showDD, setShowDD] = useState(false)
  const {languages, languagesDesc, language, languageDesc, changeLanguage} = useLanguageManage()

  const stop = useCallback((event: Event) => {
    event.stopPropagation();
    const key = (event.target as HTMLElement).getAttribute('data-key') as string

    if (key !== language) {
      changeLanguage(key);
      setShowDD(false);
    }
  }, [changeLanguage, language])

  useEffect(() => {
    const hide = () => {
      setShowDD(false)
    }
    const ref = wrapper.current
    if (ref) {
      document.addEventListener("click", hide);
      ref.addEventListener("click", stop);
    }

    return () => {
      if (ref) {
        document.removeEventListener('click', hide)
        ref.removeEventListener('click', stop)
      }
    }
  }, [setShowDD, stop])

  return (
    <div className='c-home-footer'>
      <CenterWrapper className='c-home-footer-main'>
        <div className='left'>
          <img className='logo' src={Logo} width={245} alt="logo"/>
          <div className='language-wrapper'>
            <div className='language' onClick={() => {setShowDD(true)}}>
              <img src={Language} width={26} alt="Language"/>
              <span>{languageDesc}</span>
              <img src={Arrow} width={14} alt="Language"/>
            </div>
            {showDD && (
              <div className='dropdown' ref={wrapper}>
                {languages.map((key, index) =>
                  <div key={key} data-key={key} className={cn('item', { active: language === key })}>{languagesDesc[index]}</div>
                )}
              </div>
            )}
          </div>
          <p>Copyright © 2021 Dprotocols</p>
        </div>
        {isMobile ? dls : <div className='right'>{dls}</div>}
      </CenterWrapper>
    </div>
  )
}

export default Footer;
