import React, { useState } from 'react'
import './index.scss'
import { useUserActionHandlers, useUserState } from '../../../../state/user/hooks'
import cn from 'classnames'

const slippages = [10, 50, 100]

const Setting: React.FC = () => {
    const [show, setOpenModal] = useState(false)
    const { allowedSlippage, userDeadline } = useUserState()
    const { setDeadline, setSlippage } = useUserActionHandlers()

    return (
        <div className='t-setting'>
            <button onClick={() => {
                setOpenModal(!show)
            }}>setting</button>
            <div className='setting-modal' style={{
                display: show ? 'inline-block' : "none"
            }}>
                <div className='c-row'>
                    <div className='title'>Slippage toterance</div>
                    <div className='action'>
                        <div className='slippage-tag'>
                            {
                                slippages.map(s => <button
                                    key={s.toString()}
                                    onClick={() => {
                                        setSlippage(s)
                                    }}
                                    className={cn({ active: s === allowedSlippage })}
                                >{`${s / 100}%`}</button>)
                            }
                        </div>
                        <div className='d-input'>
                            <input placeholder={allowedSlippage ? (allowedSlippage / 100).toString() : '自定义'}
                                onChange={({ target: { value } }) => {
                                    setSlippage(Number(value) * 100)
                                }}
                            />
                            <span>%</span>
                        </div>
                    </div>
                </div>
                <div className='devider' />
                <div className='c-row'>
                    <div className='title'>Transaction deadline</div>
                    <div className='d-input'>
                        <input placeholder={userDeadline?.toString() || "自定义"}
                            onChange={({ target: { value } }) => {
                                setDeadline(Number(value) || 0)
                            }}
                        />
                        <span>minutes</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Setting
