import React, {useState, useMemo, useReducer, useEffect} from 'react'
import './index.scss';
import cn from 'classnames';
import TokenList from '../component/TokenList'
import TradeHeader from "../component/TradeHeader";
import {useRequest} from "ahooks";
import {getLiquidityChartData, getLiquidityRecords, getTradeChartData, getTradeRecords} from "../../../service";
import {useList} from "../../../state/list/hooks";
import {useGlobal} from "../../../state/global/hooks";
import {amount} from "../../../utils";

type itemType = {
    title: string,
    filter: boolean
}

const SidebarConfig: { [name: string]: itemType } = {
    liquidity: {
        title: "Pool Activity",
        filter: true
    },
    trade: {
        title: "Trading History",
        filter: false
    }
}

type ConditionTypes = 0 | 1 | 2;
type LiquidityList = any[]
type LiquidityHistory = [LiquidityList, LiquidityList, LiquidityList];
type LiquidityAction = { type: ConditionTypes | -1, payload: LiquidityList }
type LiquidityReducer = React.Reducer<LiquidityHistory, LiquidityAction>
export type Condition = {text: string, type: ConditionTypes}
const initLiquidityHistory: LiquidityHistory = [[], [], []]
export const conditions: [Condition, Condition, Condition] = [
    { text: 'all', type: 0},
    { text: 'adds', type: 1},
    { text: 'removes', type: 2}
]

const reducer: LiquidityReducer = (state, action) => {
    if(action.type === -1) {
        state = initLiquidityHistory
        return state
    } else {
        state[action.type] = action.payload
        return [...state]
    }
    // return state
}

const defaultRequestOptions = {
    manual: true,
    initialData: [],
    formatResult: (data: Result) => {
        if (data?.success) return data?.data || []
        return []
    }
}

export const useLiquidityRecords = (type: 0 | 1 | 2) => {
    const {CurrentSwapLiquidity} = useList()
    const [liquidityRecords, dispatch] = useReducer<LiquidityReducer>(reducer, initLiquidityHistory);
    useEffect(() => {
        if (CurrentSwapLiquidity) {
            const {address} = CurrentSwapLiquidity;
            getLiquidityRecords(address, type).then((data: Result) => {
                if (data.success) {
                    dispatch({
                        type: type,
                        payload: data.data,
                    })
                }
            })
        } else {
            dispatch({type: -1, payload: []})
        }
    }, [CurrentSwapLiquidity, type])

    return {liquidityRecords: liquidityRecords[type]}
}

export const useTradeRecords = (key: 'liquidity' | 'trade') => {
    const {CurrentSwapLiquidity} = useList()
    const {data: tradeRecords, run: tradeRecordsRun} = useRequest(getTradeRecords, defaultRequestOptions)

    useEffect(() => {
        if (CurrentSwapLiquidity && key === 'trade') {
            const {address} = CurrentSwapLiquidity;
            tradeRecordsRun(address)
        } else{

        }

    }, [CurrentSwapLiquidity, key, tradeRecordsRun])

    return {tradeRecords}
}

