import { createAction } from '@reduxjs/toolkit'

export enum Field {
    INPUT = "INPUT",
    OUTPUT = "OUTPUT",
    BUY = "BUY",
    SELL = "SLLE"
}

export const typeInput = createAction<{ field: Field, typedValue: string }>("swap/typeInput");
export const resetInuptValue = createAction("swap/resetInuptValue");
