import React, {useState, useEffect} from "react";
import {useCountUp} from "react-countup";
import {useInterval} from 'ahooks'
import './style.scss';

const Investment: React.FC = () => {
  const [ count, setCount ] = useState(5299124715);
  const { countUp, update } = useCountUp({
    start: 0,
    end: count,
    duration: 0.2,
    separator: ",",
    decimal: ".",
    prefix: "$ "
  });

  useInterval(() => {
    setCount(count + 172343)
  }, 4000);

  useEffect(() => {
    update(count)
  }, [count, update])

  return <strong>{countUp}</strong>
}

export default Investment;


