import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom'
import './i18n';
import './index.scss';
import App from './App';
import store from './state'
import { Provider } from 'react-redux'
import { createWeb3ReactRoot, Web3ReactProvider, } from '@web3-react/core'
import getLibrary from './utils/getLibrary'
import { NetworkContextName } from './constants'

import ListsUpdater from './state/list/updater';
import ViewUpdater from "./state/view/updater";
import GlobalUpdater from "./state/global/updater";
import Multicall from "./state/multicall/updater";
import Application from "./state/application/updater";
import TransactionUpdater from './state/transactions/updater'

const Web3ProviderNetwork = createWeb3ReactRoot(NetworkContextName)
if (!!window.ethereum) {
  window.ethereum.autoRefreshOnNetworkChange = false
}

function Updaters() {
  return (
    <>
      <GlobalUpdater />
      <ListsUpdater />
      <ViewUpdater />
      <Multicall />
      <Application />
      <TransactionUpdater />
    </>
  )
}

ReactDOM.render(
  <React.StrictMode>
    <Web3ReactProvider getLibrary={getLibrary}>
      <Web3ProviderNetwork getLibrary={getLibrary}>
        <Provider store={store}>
          <Updaters />
          <HashRouter>
            <App />
          </HashRouter>
        </Provider>
      </Web3ProviderNetwork>
    </Web3ReactProvider >
  </React.StrictMode >,
  document.getElementById('root')
);

