import './style.scss'
import classNames from "classnames";
import * as React from "react";
import {ReactElement, useCallback, useEffect, useRef, useState} from "react";

interface Props {
  className?: string,
  dropContent?: ReactElement,
  autoHide?: boolean,
  onContentClick?: (e: Event) => void
}

const DropDown: React.FC<Props> = ({
 className,
 children,
 dropContent,
 autoHide = false,
 onContentClick
}) => {
  const [showDD, setShowDD] = useState(false)
  const content = useRef<HTMLDivElement | null>(null)

  const stop = useCallback((event: Event) => {
    !autoHide && event.stopPropagation();
    onContentClick && onContentClick(event)
  }, [autoHide, onContentClick])

  useEffect(() => {
    const hide = () => {
      setShowDD(false)
    }
    const ref = content.current

    if (ref) {
      document.addEventListener("click", hide);
      ref.addEventListener("click", stop);
    }

    return () => {
      if (ref) {
        document.removeEventListener('click', hide)
        ref.removeEventListener('click', stop)
      }
    }
  }, [setShowDD, stop, showDD])

  return (
    <div className={classNames('c-dropdown', { active: showDD, [className as string]: !!className})}>
      <div className='c-dropdown-btn' onClick={() => {setShowDD(true)}}>{children}</div>
      {showDD && <div className='c-dropdown-content' ref={content}>{dropContent}</div>}
    </div>
  )
}

export default DropDown;
