import React, { useState } from 'react'
import Icon from '../../../Icon'
import './index.scss'
import cn from 'classnames'
import Cookies from 'js-cookie'
// import i18next from 'i18next'
import { useTranslation } from "react-i18next";

type SettingModalProps = {
    onClose?: Function
}

const SettingModal: React.FC<SettingModalProps> = ({ onClose }) => {
    const [isOn, setOn] = useState<boolean>(false)
    const { t, i18n } = useTranslation()

    const changeLanguage = async (key: string) => {
        await i18n.changeLanguage(key)
        Cookies.set('language', key)
    }

    return (
        <div className='setting-modal'>
            <div className='title'>{t('settings')}</div>
            <div className='setting-cont'>
                <div className="cont-item">
                    <div className='label'>
                        <div className='subtitle'>Push Notifications</div>
                        <p>Stay up-to-date with your favorites trading pairs when market moves</p>
                    </div>
                    <div className='action'>
                        <button role='on-off' className={cn({ on: isOn })} onClick={() => setOn(!isOn)} />
                    </div>
                </div>
                <div className="cont-item">
                    <div className='label'>
                        <div className='subtitle'>Price Changes Threshold</div>
                        <p>When market moves, +/-</p>
                    </div>
                    <div className='action'>
                        <div className='percent'>
                            <input value="20%" />
                        </div>
                    </div>
                </div>
                <div className="cont-item">
                    <div className='label'>
                        <div className='subtitle'>Liquidity Changes Threshold</div>
                        <p>When pair’s liquidity changes, +/-</p>
                    </div>
                    <div className='action'>
                        <div className="percent">
                            <input value="20%" />
                        </div>
                    </div>
                </div>
                <div className="cont-item language">
                    <div className='label'>
                        <div className='subtitle'>Language</div>
                    </div>
                    <div className='action'>
                        <div className='links' >
                            {[
                                ['English', 'en'],
                                // ['中文', 'zh'],
                                // ['日本語', 'ja'],
                                // ['한국어', 'ko']
                            ].map(([text, key]) =>
                                <a key={key} onClick={() => { changeLanguage(key) }} className={cn({ active: i18n.language === key })}>{text}</a>
                            )}
                        </div>
                    </div>
                </div>
                <div className="cont-item">
                    <div className='label'>
                        <div className='subtitle'>Join Our Community</div>
                        <div className='links'>
                            <a>
                                <Icon size={22} symbol='discord' />
                                <span>Discord</span>
                            </a>
                            <a>
                                <Icon size={22} symbol='twitter' />
                                <span>Twitter</span>
                            </a>
                            <a>
                                <Icon size={20} symbol='telegram' />
                                <span>Telegram</span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div className='modal-footer'>
                <button onClick={() => { onClose && onClose() }}>{t('close')}</button>
            </div>
        </div>
    )
}

export default SettingModal
