import {useDispatch, useSelector} from 'react-redux'
import {AppDispatch, AppState} from '../index'
import {LocalState} from "./reducer";
import {setSelectedToken as setToken, toggleLikedToken as toggleToken} from './actions'

interface useLocalTypes extends LocalState{
  setSelectedToken: (token: string) => void
  toggleLikedToken: (token: string) => void
}

export function useLocal(): useLocalTypes {
  const dispatch = useDispatch<AppDispatch>()
  const setSelectedToken = (token: string) => {
    dispatch(setToken(token))
  }
  const toggleLikedToken = (token: string) => {
    dispatch(toggleToken(token))
  }
  return {
    ...useSelector((state: AppState) => state.local),
    setSelectedToken,
    toggleLikedToken,
  }
}
